:root {
  --orange-dark: #D87D4A;
  --coal: #101010;
  --black: #000000;
  --white: #ffffff;
  --gray-dark: #f1f1f1;
  --grey-light: #fafafa;
  --orange-light: #FBAF85;
}

* {
  box-sizing: border-box;
  color: black;
  padding: 0;
  margin: 0;
}

main {
  min-height: calc(100vh - 365px + 90px);
}

.section {
  width: 1100px;
  margin: 100px auto;
}

body {
  overflow-x: hidden;
}

.skip-to-content-link {
  position: absolute;
  top: 20px;
  left: -500px;
  opacity: 0;
}

.skip-to-content-link:focus {
  z-index: 9999;
  position: absolute;
  top: 20px;
  left: 20px;
  overflow: auto;
  width: auto;
  height: auto;
  clip: auto;
  visibility: none;
  opacity: 1;
  background: black;
  color: white;
  text-decoration: none;
  padding: 10px 20px;
}

body {
  font-family: "Manrope", sans-serif;
  color: var(--black);
  font-size: 15px;
  line-height: 25px;
}

h1 {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 56px;
  line-height: 58px;
  letter-spacing: 2px;
}

h2 {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 40px;
  line-height: 44px;
  letter-spacing: 1.5px;
}

h3 {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 32px;
  line-height: 36px;
  letter-spacing: 1.15px;
}

h4 {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 2px;
}

h5 {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 1.7px;
}

h6 {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 1.3px;
}

.overline {
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 10px;
  color: var(--orange-dark);
  font-weight: 400;
  text-transform: uppercase;
}

.sub-title {
  font-size: 13px;
  line-height: 25px;
  letter-spacing: 1px;
  color: var(--orange-dark);
  font-weight: 900;
  text-transform: uppercase;
}

.button-primary {
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1px;
  padding: 16px 30px;
  background-color: var(--orange-dark);
  color: var(--white);
  display: inline-block;
  text-transform: uppercase;
  cursor: pointer;
}
.button-primary:hover {
  background-color: var(--orange-light);
}

.button-secondary {
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1px;
  padding: 16px 30px;
  background-color: var(--white);
  border: 1px solid var(--black);
  color: var(--black);
  display: inline-block;
  text-transform: uppercase;
  cursor: pointer;
}
.button-secondary.black {
  background-color: var(--black);
  color: var(--white);
  border: none;
}
.button-secondary.black:hover {
  background-color: #1a1a1a;
}
.button-secondary:hover {
  background-color: var(--black);
  color: var(--white);
}

.button-tertiary {
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1px;
  padding: 16px 30px;
  color: var(--black);
  display: inline-block;
  text-transform: uppercase;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}
.button-tertiary span {
  margin-right: 10px;
}
.button-tertiary:hover span {
  color: var(--orange-dark);
}

.header {
  height: 90px;
  width: 100%;
  overflow: hidden;
  background-color: black;
}
.header__wrapper {
  height: 100%;
  max-width: 1100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .header__wrapper {
    margin: 0 20px;
  }
}
.header__logo {
  font-size: 25px;
  font-weight: 900;
  text-transform: lowercase;
  color: var(--white);
  display: flex;
  align-items: center;
}
.header__logo div {
  display: none;
  cursor: pointer;
}
@media (max-width: 768px) {
  .header__logo div {
    display: block;
  }
}
.header__logo div svg {
  height: 16px;
  width: 16px;
  margin-right: 40px;
}
.header-menu.menu-active {
  left: 0;
}
.header-menu {
  z-index: 1000;
  display: flex;
  list-style-type: none;
}
@media (max-width: 768px) {
  .header-menu {
    position: absolute;
    background-color: var(--white);
    top: 90px;
    left: 100%;
    height: calc(100vh - 90px);
    width: 100vw;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.25s ease-in;
  }
}
.header-menu--item a {
  color: var(--white);
  text-decoration: none;
  font-weight: 900;
  line-height: 25px;
  letter-spacing: 2px;
  font-size: 13px;
}
@media (max-width: 768px) {
  .header-menu--item a {
    color: var(--black);
    font-size: 30px;
  }
}
.header-menu--item a:hover {
  color: var(--orange-dark);
}
.header-menu--item:not(:last-child) {
  margin-right: 20px;
}
@media (max-width: 768px) {
  .header-menu--item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

footer {
  height: 365px;
  background: var(--black);
  color: var(--white);
}
footer .content-wrapper {
  max-width: 1110px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  padding: 50px 0;
}
footer .content-wrapper .left-col {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
footer .content-wrapper .left-col div,
footer .content-wrapper .left-col p {
  color: var(--white);
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 0px;
}
footer .content-wrapper .left-col .shop-name {
  font-size: 25px;
  font-weight: 900;
  text-transform: lowercase;
  color: var(--white);
  margin-bottom: 20px;
}
footer .content-wrapper .right-col {
  width: 50%;
}

.hero-section {
  background: var(--coal);
  height: 650px;
  min-height: 650px;
  position: relative;
}
.hero-section .border-top {
  width: 1110px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 1px;
  background-color: #ffffff;
  opacity: 0.2;
}
.hero-section .content-wrapper {
  max-width: 1110px;
  margin: 0 auto;
  display: flex;
  height: 100%;
}
.hero-section .content-wrapper .col-left {
  width: 50%;
  flex: 1;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hero-section .content-wrapper .col-left .pre-title {
  color: var(--white);
  margin-bottom: 20px;
}
.hero-section .content-wrapper .col-left h1 {
  color: var(--white);
  margin-bottom: 20px;
}
.hero-section .content-wrapper .col-left p {
  color: var(--white);
}
.hero-section .content-wrapper .col-right {
  width: 50%;
  display: flex;
  flex-direction: column;
}
.hero-section .content-wrapper .col-right svg {
  height: 100%;
  width: 100%;
  fill: white;
}

.section-nav {
  display: flex;
  justify-content: space-between;
}
.section-nav__item {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--gray-dark);
  padding: 35px 0;
  border-radius: 5px;
}
.section-nav__item h6 {
  padding-top: 20px;
}

.section-grid-container {
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.section-grid-container .grid-item-large {
  width: 100%;
  height: 580px;
  overflow: hidden;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section-grid-container .grid-item-large h2 {
  margin-bottom: 20px;
}
.section-grid-container .grid-item-large p {
  margin-bottom: 20px;
}
.section-grid-container .grid-item-medium {
  width: 100%;
  height: 320px;
  overflow: hidden;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section-grid-container .grid-item-medium h2 {
  margin-bottom: 20px;
}
.section-grid-container .grid-item-medium p {
  margin-bottom: 20px;
}
.section-grid-container .grid-two-col {
  width: 100%;
  height: 320px;
  overflow: hidden;
  border-radius: 5px;
  display: flex;
  gap: 20px;
}
.section-grid-container .grid-two-col h2 {
  margin-bottom: 20px;
}
.section-grid-container .grid-two-col p {
  margin-bottom: 20px;
}
.section-grid-container .grid-two-col--left {
  height: 100%;
  width: 50%;
}
.section-grid-container .grid-two-col--right {
  height: 100%;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}